* {
  box-sizing: border-box;
}

.app-wrapper{
  display: grid;
  grid-template-areas:
  'h h'
  'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.app-wrapper-content{
  grid-area: c;
  background-color: #989696;
}



